@font-face {
  font-family: "InterFace";
  src: url("../../assets/fonts/InterFace-Bold.eot");
  src: local("InterFace Bold"), local("InterFace-Bold"),
    url("../../assets/fonts/InterFace-Bold.eot?#iefix")
      format("embedded-opentype"),
    url("../../assets/fonts/InterFace-Bold.woff2") format("woff2"),
    url("../../assets/fonts/InterFace-Bold.woff") format("woff"),
    url("../../assets/fonts/InterFace-Bold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
}

